<template>
  <div>
    <div class="container">
      <div class="card" style="width: 50rem; margin-left: 150px">
        <img class="card-img-top" :src="image" alt="Card image cap" />
        <div class="card-body">
          <h1 class="card-title">{{ this.facet }}</h1>
          <h5 class="card-title">{{ this.covid[0].Date }}</h5>

          <table class="table">
            <thead class="thead-dark">
              <tr>
                <th scope="col">{{ this.facet }}</th>
                <th scope="col">Country</th>
              </tr>
            </thead>
            <tbody v-if="this.facet == 'NewConfirmed'">
              <tr v-for="c in this.filterCountries" :key="c.Country">
                <td>{{ c.NewConfirmed }}</td>
                <td>{{ c.Country }}</td>
              </tr>
            </tbody>
            <tbody v-if="this.facet == 'NewDeaths'">
              <tr v-for="c in this.filterCountries" :key="c.Country">
                <td>{{ c.NewDeaths }}</td>
                <td>{{ c.Country }}</td>
              </tr>
            </tbody>
            <tbody v-if="this.facet == 'NewRecovered'">
              <tr v-for="c in this.filterCountries" :key="c.Country">
                <td>{{ c.NewRecovered }}</td>
                <td>{{ c.Country }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    facet: String,
    numCountries: String
  },
  data() {
    return {
      covid: [],
      countries: [],
      selectedCountry: "Portugal",
      image: require("../assets/coronavirus.jpg")
    };
  },
  async created() {
    this.getAllInfo();
  },
  async updated() {
    this.getAllInfo();
    if (this.facet === "NewConfirmed") {
      this.image = require("../assets/coronavirus.jpg");
    } else if (this.facet === "NewDeaths") {
      this.image = require("../assets/candle.jpg");
    } else {
      this.image = require("../assets/hug.jpg");
    }
  },
  methods: {
    async getAllInfo() {
      const response = await fetch("https://api.covid19api.com/summary");
      const myData = await response.json();
      this.covid = myData.Countries;
      if (this.facet == "NewConfirmed") {
        this.image = require("../assets/coronavirus.jpg");
        this.covid.sort(this.compareConfirmed);
      }
      if (this.facet == "NewDeaths") {
        this.image = require("../assets/candle.jpg");
        this.covid.sort(this.compareDeaths);
      }
      if (this.facet == "NewRecovered") {
        this.image = require("../assets/hug.jpg");
        this.covid.sort(this.compareRecovered);
      }
    },
    compareConfirmed(a, b) {
      if (a.NewConfirmed > b.NewConfirmed) return -1;
      if (a.NewConfirmed < b.NewConfirmed) return 1;
      else return 0;
    },
    compareDeaths(a, b) {
      if (a.NewDeaths > b.NewDeaths) return -1;
      if (a.NewDeaths < b.NewDeaths) return 1;
      else return 0;
    },
    compareRecovered(a, b) {
      if (a.NewRecovered > b.NewRecovered) return -1;
      if (a.NewRecovered < b.NewRecovered) return 1;
      else return 0;
    }
  },
  computed: {
    filterCountries() {
      return this.covid.slice(0, this.numCountries);
    }
  }
};
</script>
